import React from "react";
import Button from '@material-ui/core/Button';

class AnimalCounter extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      count: 0,
    };
  }

  render() {
    const unit_prefix =
      this.state.count === 0
        ? "ひ"
        : ["ぴ", "ぴ", "ひ", "び", "ひ", "ひ", "ぴ", "ひ", "ぴ", "ひ"][this.state.count % 10];
    const unit = `${unit_prefix}き`;

    return (
      <Button
        variant="contained"
        onClick={() => {
          this.setState({
            count: this.state.count + 1,
          })
        }}
      >
        {`${this.props.animal_name}が${this.state.count}${unit}`}
      </Button>
    );
  }
}

export default AnimalCounter;

