import React from "react";
import Button from '@material-ui/core/Button';

class NumberReasoner extends React.Component {
  constructor(props){
    super(props);

    this.initial_state = {
      upper: props.upper,
      under: props.under,
    };

    this.state = {
      ...this.initial_state,
    };
  }

  render() {
    const is_initial_condition =
      this.state.upper === this.initial_state.upper
      && this.state.under === this.initial_state.under;
    const is_finished =
      this.state.upper === this.state.under;

    const middle_number = Math.ceil((this.state.upper + this.state.under) / 2);
    const upper_button =
      <Button
        onClick={() => this.setState({
          under: middle_number,
        })}
      >
        {`${middle_number}以上`}
      </Button>;
    const under_button =
      <Button
        onClick={() => this.setState({
          upper: middle_number - 1,
        })}
      >
        {`${middle_number}未満`}
      </Button>;

    const reset_button =
      <Button
        variant="contained"
        onClick={() => {
          this.setState({
            ...this.initial_state,
          })
        }}
      >
        リセット
      </Button>;

    const answer_message =
      <p>
        {`あなたの思い浮かべた数字は${middle_number}です！`}
      </p>;

    return (
      <div>
        <p>
          {`${this.initial_state.under}から${this.initial_state.upper}までの整数を思い浮かべてください！`}
        </p>
        {
          !is_finished
            ? <div>
                <p>それは…</p>
                {upper_button}
                {under_button}
              </div>
            : null
        }
        {is_finished ? answer_message : null}
        {!is_initial_condition ? reset_button : null}
      </div>
    );
  }
}

export default NumberReasoner;

